body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper-1374 {
  max-width: 1374px;
  margin: 0 auto;
  padding: 0 15px;
}
.wrapper-1320 {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (max-width: 1200px) {
    .pr-banner .banner-text {
        padding: 0 15px !important;
    }
    .Send-Gift-heading {
        white-space: inherit !important;
    }
    .img-text {
        margin-right: 0px !important;
    }
    .confession_stories .confession_stories_right .swiper-container {
      padding: 20px 0 !important;
    }
    .mgift-listing-inner li img {
      max-width: 100% !important;
    }
}
@media screen and (max-width: 991px) {
  .confession_stories .confession_stories_right .swiper-container {
    padding: 10px 0 !important;
  }
}
@media screen and (max-width: 768px) {
  .gift-listing-inner {
      flex-wrap: wrap !important;
  }
  .gift-listing-inner li {
      width: 49% !important;
  }
  .mgift-listing {
    overflow-y: hidden !important;
  }
  .mgift-listing-inner {
    padding: 0 !important;
    min-width: 768px !important;
  }
  .mgift-listing-inner li {
    margin-top: 30px !important;
    min-width: 150px !important;
  }
  .confession_stories .confession_stories_inner {
    flex-wrap: wrap !important;
  }
  .confession_stories .confession_stories_left {
    max-width: 100% !important;
    margin-left: 0 !important;
  }
  .confession-image .frame {
    width: 80% !important;
  }
  .confession-image .banner-img img {
    width: 62% !important;
    max-width: 62% !important;
  }
  .confession_stories .confession_stories_right {
    max-width: 100% !important;
    padding-left: 20px !important;
    margin-top: -100px !important;
    padding-top: 100px !important;
  }
  .confession_stories_right .swiper-button-prev, 
  .confession_stories_right .swiper-button-next {
    display: none !important;
  }
  .confession_stories .confession_stories_right p {
    font-size: 15px !important;
  }
  .confession_stories .confession_stories_right a {
    margin-top: 30px !important;
  }
  .confession_stories .confession_stories_right .swiper-container {
    padding: 40px 0 !important;
  }
  .confession-image .frame {
    -webkit-transform: translate(-0%, -4%) !important;
            transform: translate(-0%, -4%) !important;
  }
}

@media screen and (max-width: 575px) {
  /* .pr-banner {
    height: 50vh;
  } */
  .pr-banner .swiper-button-prev, .pr-banner .swiper-button-next {
      display: none !important;
  }
  .bg-heart-image {
    padding: 0 0 0 !important;
  }
  .mgift-listing-inner {
    padding: 0 !important;

  }
  .mgift-listing-inner li {
    margin-top: 30px !important;
  }
  .emotions-content {
    text-align: center !important;
    padding: 40px 0 !important;
  }
  .emotions-column-1 {
    flex-direction: column !important;
  }
  .emotions-column-1 div {
    /* padding: 0 0 !important; */
  }
}
@media screen and (max-width: 425px) {
  .banner-text .banner-heading {
    font-size: 24px !important;
  }
  .banner-text p {
    font-size: 15px !important;
  }
  .banner-text a {
    font-size: 15px !important;
  }
  .bg-pink {
    margin-top: 0 !important;
  }
  .pr-banner .swiper-pagination {
    bottom: 3% !important;
  }
  .Send-Gift-heading {
    font-size: 30px !important;
  }
  .confession_stories .confession_stories_right h4 {
    font-size: 30px !important;
  }
  .heading-and-img {
    flex-wrap: wrap;
    flex-direction: column-reverse;
  }
  .heading-and-img .rose-day-img-text {
    align-self: flex-end;
  }
  .gift-listing-inner {
    margin-top: 10px !important;
  }
  .gift-listing-inner li {
    padding-top: 10px !important;
  }
  .gift-listing-inner li p,
  .mgift-listing-inner li p {
    font-size: 15px !important;
  }
  .confession-image .frame {
    width: 90% !important;
  }
  .confession-image .banner-img img {
    width: 70% !important;
    max-width: 70% !important;
  }
}
@media screen and (max-width: 375px) {
  .pr-banner .banner-text {
    max-width: 80% !important;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.pr-banner .swiper-button-prev:after, 
.pr-banner .swiper-button-next:after {
    font-size: 25px;
    font-weight: 700;
    color: #000;
}
.pr-banner .swiper-wrapper {
    z-index: -1;
}
.pr-banner .swiper-slide {
    z-index: -1;
}
.pr-banner .swiper-pagination {
    bottom: 20%;
}

.pr-banner .swiper-pagination-bullet {
    background: #ec3f78;
}

.pr-banner .banner-text {
    position: absolute;
    top: 10px;
    max-width: 60%;
    text-align: left;
}

.banner-text .banner-heading {
    font-family: 'Playfair Display', serif;
    font-size: 40px;
    font-weight: 700;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: normal;
    text-align: left;
    color: #ec407c;
    margin: 0;
}

.banner-text p {
    font-family: 'Lato', sans-serif;
    font-size: 20px;
    font-weight: 600;
    font-stretch: normal;
    font-style: italic;
    color: #000;
    text-align: left;
    margin: 0;
    margin-top: 5px;
}

.banner-text a {
    font-family: 'Lato', sans-serif;
    font-style: italic;
    font-weight: 600;
    font-size: 30px;
    padding: 5px 30px;
    background: #ec407c;
    color: #fff;
    border-radius: 15px;
    margin-top: 10px;
    display: inline-block;
}
.pink-section {
    position: relative;
    z-index: 1;
}
.header {
    width: 100%;
    height: 50px;
    background-color: gray;
    color: #fff;
    text-align: center;
    font-size: 30px;
    font-weight: 700;
    padding: 0px 0;
}
.bg-pink {
    min-height: 500px;
    margin-top: -7%;
    padding: 25px 25px 25px 25px;
    border-radius: 15px;
    background-image: linear-gradient(to bottom, #f8447e, #d92a62);
}

.Send-Gift-heading {
    -webkit-text-stroke: 1px #fff;
    font-family: 'Courgette', cursive;
    font-size: 36px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.25;
    letter-spacing: normal;
    text-align: left;
    color: #fff;
    white-space: nowrap;
}
.Send-Gift-subheading {
    font-family: 'Lato', sans-serif;
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: italic;
    line-height: 1.2;
    letter-spacing: normal;
    text-align: left;
    color: #fff;
}

.heading-and-img {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

}

.img-text {
    width: 125px;
    height: 125px;
    background: #ffffff;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid #dbb868;
    position: relative;
    margin-top: -86px;
    margin-right: 50px;
}

.date {
    -webkit-text-stroke: 1px #f5437c;
    font-family: Courgette;
    font-size: 22px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: 1.5px;
    text-align: center;
    color: #f5437c;
}
.day {
    -webkit-text-stroke: 1px #010000;
    font-family: Courgette;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: 1px;
    color: #000;
}

.img-text img {
    max-width: 80%;
    margin: 0 auto;
}
.img-text::before {
    position: absolute;
    content: '';
    width: 125px;
    height: 125px;
    left: -5px;
    top: -5px;
    border-radius: 50%;
    background: transparent;
    border: 1px solid #dbb868;
}
.gift-listing-inner {
    margin: 20px 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    background: #ffffff;
    padding: 20px;
    list-style: none;
    border-radius: 15px;
}

.gift-listing-inner li {
    width: 24%;
}

.gift-listing-inner li img {
    border-radius: 15px;
    width: 100%;
}

.gift-listing-inner li p {
    margin: 15px 0 0;
    font-family: 'Lato', sans-serif;
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: normal;
    text-align: center;
    color: #3a2414;
    text-transform: uppercase;
}
.mgift-listing-inner {
    margin: 0 0 0;
    margin-top: -20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #ffffff;
    padding: 20px;
    list-style: none;
    border-radius: 5px;
    flex-wrap: wrap;
}

.mgift-listing-inner li {
    width: 24%;
    margin-top: 40px;
    border-radius: 15px;
}

.mgift-listing-inner li img {
    border-radius: 15px;
    transition: border-radius ease-in-out 0.3s;
}
.mgift-listing-inner li:hover img {
    border-radius: 50%;
    transition: border-radius ease-in-out 0.3s;
}

.mgift-listing-inner li p {
    margin: 15px 0 0;
    font-family: 'Lato', sans-serif;
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: normal;
    text-align: center;
    color: #3a2414;
    text-transform: uppercase;
}
.bg-heart-image {
    padding: 0px 2.2% 0px;
}
.bg-heart-image-inner {
    background: url(/bakingo/static/media/background-heart.09232431.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    padding: 75px 2.2% 75px;
}
.bg-heart-image-inner h4 {
    font-weight: 700;
    font-size: 26px;
}
.confession_stories {
    padding: 100px 0 50px;
}
.confession_stories .confession_stories_inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.confession-image {
    position: relative;
    padding: 0px;
}
.confession-image .frame {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 2;
    width: 140%;
    -webkit-transform: translate(-14.8%, -3.5%);
            transform: translate(-14.8%, -3.5%);
}
.confession_stories .confession_stories_left {
    max-width: 25%;
    margin-left: 3%;
}
.confession_stories_left .swiper-button-prev, .confession_stories_left .swiper-button-next {
    display: none;
}

.confession-image .swiper-pagination {
    display: none;
}
.confession_stories .confession_stories_right {
    max-width: 72%;
    padding: 10px 20px;
    padding-left: 7%;
    background: #e33d74;
    box-shadow: -5px 0px 20px 5px rgba(87,84,84,0.75) inset;
    -webkit-box-shadow: -5px 0px 20px 5px rgba(87,84,84,0.75) inset;
    -moz-box-shadow: -5px 0px 20px 5px rgba(87,84,84,0.75) inset;
    border-radius: 15px;
}

.confession_stories_right .banner-text {
    max-width: 90%;
    margin: 0 auto;
}
.confession_stories .confession_stories_right h4 {
    font-family: Courgette;
    font-size: 50px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.26;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
}
.confession_stories .confession_stories_right p {
    font-family: 'Rubik', sans-serif;
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
} 
.confession_stories .confession_stories_right a {
    display: inline-block;
    padding: 5px 30px;
    background: #ffffff;
    color: #e33d74;
    text-transform: uppercase;
    border-radius: 25px;
    font-size: 15px;
    text-decoration: none;
    transition: 0.3s ease;
    border: 1px solid #ffffff;
    font-style: normal !important;
}
.confession_stories .confession_stories_right a:hover {
    background: #e33d74;
    color: #ffffff;
    border: 1px solid #ffffff;
    transition: 0.3s ease;
}
.confession_stories .confession_stories_right .swiper-container {
    padding: 40px 0px;
}
.confession_stories .confession_stories_right .swiper-pagination {
    position: absolute;
    bottom: 10px;
}

.confession_stories_right .swiper-pagination-bullet {
    background: #ffffff;
    opacity: 0.5;
}
.confession_stories_right .swiper-pagination-bullet-active {
    opacity: 1;
}

.confession_stories_right .swiper-button-prev:after, 
.confession_stories_right .swiper-button-next:after {
    font-size: 25px;
    font-weight: 700;
    color: #ffffff;
}
.emotions-section {
    padding: 40px 0;
}
.emotions-section-middle {
    padding-top: 20px;
}
.emotions-section-inner {
    border: 2px solid #ccc;
    padding: 0 8px 8px;
}
.emotions-row { /* IE10 */
    display: flex; /* IE10 */
    flex-wrap: wrap;
    margin: 0 -4px;
    position: relative;
    z-index: -2;
}
.emotions-column { /* IE10 */
    flex: 50% 1;
    max-width: 50%;
    padding: 0 4px;
    display: flex;
    flex-direction: column;
}
.emotions-column img {
    margin-top: 8px;
    vertical-align: middle;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.emotions-content {
    padding: 60px 40px;
    background: #f08080;
    text-align: left;
    color: #fff;
    margin-top: -30px;
    z-index: -1;
    width: 130%;
}
.emotions-content h4 {
    font-family: 'Playfair Display', serif;
    font-size: 26px;
    font-weight: 700;
}
.emotions-content p {
    font-size: 14px;
    margin: 0;
    margin-top: 15px;
}
.emotions-content a {
    padding: 8px 30px;
    margin-top: 20px;
    font-size: 14px;
    background: #fff;
    border-radius: 0;
    text-decoration: none;
    display: inline-block;
    color: #f08080;
}
.emotions-column-1 {
    display: flex;
    margin: 0 -4px;
    flex-grow: 1;
}
.emotions-column-1 div {
    padding: 0 4px;
    display: flex;
    flex-direction: column;
}
.emotions-column-1 div img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
